	module.exports = {"data":{"markdownRemark":{"html":"<p>魔王の侵略が続いていたキンザブロウ王国は、ついに限界がやってきた。王国軍は破滅し、食料も底を尽き、国民は家にこもり恐怖に耐え続けていたかも。 そんな中、国の破滅を悟った3代目国王のタゴサクは悩んだ末最後の切り札である屈指の勇者、ライディーン＝シャドウ＝ドロンパ(以下ドロン)を召喚し、魔王の処刑を命じた。指令を受けたドロンは「3……2……1……ドドンパ！」と叫び、時速170km/h位で城を後にした。 魔王は予想外に強く、さすがのドロンでもそこそこ苦労したっぽい。 「く……こうなったら、かったるいけどキンザブロウの剣を使うのみ！」 ドロンは刃渡り 12cm の剣を抜くと小声で呪文を詠唱しながら魔王に切りかかる。 「剣よ……今こそ我に力を……！伝説の封印魔法、オマエノカァチャンデベソ！」 ズ　バ　ッ　！ ドロンは宙返りしながら魔王のカツラを一刀両断！その瞬間、魔王の動きが止まる。 「きさま……私のヅラ……いくらかかったと思っているのか！！」 ついに本気を出した魔王は「あるもの」を取り出し、それにナイフを突きつけた。すると、今度はドロンの動きが止まったではないか！ 「な……それは……魔王ッ！お前はどこまで卑怯な手を使うのか！」 「ふふふ、1歩でもそこを動いたら、今日のおやつはナシだぞ」 魔王の手の内にあるものは、まさしくドロンの手提げ紙袋に入っていた銘菓「ひよ子」だった！魔王は身動きの取れないドロンを見やると、側近のカルガモをけしかけた。 「さあ！動けぬ貴様は魔王親衛隊々長、カルガモに食われておしまいだ！」 カルガモはドロンに標的を定めると翼を広げもの凄い勢いで駆け出し、魔王の手のひらにあった「ひよ子」を一瞬で食べつくしてしまった！ 「しまった！こいつはおやつが好きなのを忘れていた！」 「……魔王！お前は絶対許さない！おやつの恨みを思い知れ！！！」 目の前でおやつを食われたドロンは逆上し持参した紙袋からチョトスを取り出し1カップ食いながら魔王の懐に突っ込むと、とてつもない早業で柔道風の寝技を決めた。 「く、こしゃくなッ！」 魔王はとっさに歯磨きをするがびくともしない。歯磨き粉が足りなさ過ぎる！ 「魔王、とどめだ……」 ドロンは歯ブラシを奪い取るとそれで魔王の口をこじ開け、魔王の口の中に先ほど反芻していたチョトスを吐き出した！ 「ぐ、ぐぎゃ゛ぁぁぁぁぁぁぁぁああああああ！！！！」 魔王は断末魔の叫びを上げるとそれっきり動かなくなった。 ついに、魔王は滅びた。 ドロンは都合良く昇って来た日の光を受け、安堵の表情を浮かべた……が。 「くくく……さすがは、勇者よ。この私を、亡き者にしてくれるとは……」 「ま、魔王！」 ドロンは突然の魔王の声に驚き振り返ると、そこには魔王の幽霊が！ 「私は、確かに負けた。だが、貴様を道連れにしてやる！！！！」 魔王が叫びだすと同時に彼の骸から目が眩まんばかりの光があふれ出す…… それから 1 年、ついにドロンは帰ってこなかった。城下町から魔の手は失せ、順調に再建に向かっていたが、国王は浮かばれない表情をしていた。 「ドロンよ……どこで道草しているのだ……？おい大臣、ドロンはどこだ！」 「1 年も帰ってきませんですし……残念ながら、恐らくドロン様は……」 「そんなはずはなかろう！もう良い、わしがじきじきに探し出してやる！」 国王は大臣の言葉にも耳を貸さず身支度をしそのまま城を後にした。 それからさらに 1 年、魔王が滅びてから 2 年の月日が流れた。やはり、ドロンは帰ってくることはなかった。大臣はもはやあきらめ、部下に勇者の墓を作らせていた。墓の最上部には西の方角を向いてすがすがしい顔で歯磨きをしているドロンの銅像が祭られた。そんなある日、大臣が墓に花を供えているところにぼろをまとった年老いた流浪人が墓の前にやってきた。全身汚れきっているがその顔つきに大臣は覚えがあった。 「こ……国王様？」 「さよう、1年ぶりだな」 その晩、国王の帰還を祝してパーティが行なわれた。城のホールに豪華料理が並び、宮廷音楽隊が厳かなオーケストラを奏でる。 「今宵はわしの為、このような盛大なパーティを開いてくれて光栄だ」 国王がステージに上がり挨拶をすると一斉に歓声が上がった。 「え、お静かに。わしは1年間、亡き勇者を探し世界中をさ迷い歩いたが、しかし、彼は結局見つからなかった。恐らく大臣の言うとおり苦戦の末魔王を倒したものの、帰路に着く前に命潰えたのだろう……よって、わしは代わりに彼の遺留品を持ってきた……見るが良い」 そう言うと、国王は懐から包みを取り出しふろしきを解き始める。その瞬間、ホール内に異臭とざわめきが巻き起こる。 「見よ、これぞ彼の遺留品、勇者のウ○コだ！」 「こ、国王様がご乱心なされたーーーー！！！」 　　　　　　　　　　　　　　　　　　　　　　完</p>","frontmatter":{"cover":null,"date":"2002-10-01T00:00:00+09:00","strDate":"2002/9/30","title":"本文"}}},"pathContext":{"path":"/ja/blog/2002/10/01-本文/","langKey":"ja"}}

